$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;


.fb-accordion-item-image-upload-wrapper {
	.components-base-control__field {
		position: relative;

		&:hover {
			.editor-post-featured-image__actions {
				opacity: 1;
			}
		}

		.editor-post-featured-image__actions {
			width: 100%;
			height: unset;
			padding: 8px 12px;
			text-align: center;
			justify-content: center;
			z-index: 1;

			&.active {
				background-color: $secondary_color;
				color: $primary_color;
				border-radius: 3px;
			}
		}
	}
}
.fb-accordion-item-title-wrapper {
    background: #EBF5FF;
    display: flex;
    align-items: center;
	justify-content: space-between;
    flex-direction: row;
    cursor: pointer;
    gap: 16px;
    padding: 20px 20px 20px 20px;
    border-style: none;
    border-width: 0px 0px 0px 0px;
    border-radius: 8px 8px 8px 8px;
    transition: all .3s ease-in-out;
}
.fb-accordion-title-inner {
    display: inline-flex;
    align-items: center;
    gap: 20px;
    width: 100%;
}
.fb-accordion-icon {
	width: 22px;
	height: 22px;

	svg {
		width: 100%;
		height: 100%;
	}
}

